'use strict';
import React from 'react';
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import settings from '../../util/settings';
import ReactGA from 'react-ga';
import Feature from '../../components/Feature/Feature';

class WhoAmI extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      analyticsSubmitted: {
        scroll: false
      }
    }
    this.ASSET_PATH = settings.ASSET_PATH;
    this.AnimationElements = []
  }

  componentDidMount() {
    TweenMax.to(this.container, 0.5, {autoAlpha: 1});
    TweenMax.set(this.AnimationElements, {autoAlpha: 0});
    this.props.refSetter(this.container)
    // this.props.refSetter(this.container, this.props.refOrder)
    window.addEventListener('scroll', this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    TweenMax.set(this.container, {autoAlpha: 0});
  }
  handleInputChange = (e, item)=>{

  }
  handleScroll = (e) => {
    if (this.container.getBoundingClientRect().top < window.innerHeight && !this.state.analyticsSubmitted.scroll) {
      ReactGA.modalview('work');
      this.setAnalyticSubmission('scroll')
    }
    // if (this.props.isResponsive) {
      if (this.container.getBoundingClientRect().top <= window.innerHeight/2) {
        this.animateIn();
        window.removeEventListener('scroll', this.handleScroll);
      }
    // } else {
    //   if (this.container.getBoundingClientRect().top <= 10) {
    //     this.animateIn();
    //     window.removeEventListener('scroll', this.handleScroll);
    //   }
    // }
  }
  setAnalyticSubmission = (name) => {
    if (name) {
      this.setState({analyticsSubmitted: {...this.state.analyticsSubmitted, [name]: true}})
    }
  }
  animateIn = () => {
    var tl = new TimelineMax({paused: true});
    tl.staggerFromTo(this.AnimationElements, 2, {autoAlpha: 0, y: 50}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 1);
    tl.play();
  }
  appendElement = (el) => {
    if (el) this.AnimationElements.push(el)
  }
  render() {
    this.AnimationElements = []
    var features = [
      {
        icon: ['fab', 'react'],
        text: 'An ambitious, detail-oriented and goal-driven Software Developer. Time management and organizational skills are two of my strongest skills when it comes to tackling down tasks.'
      },{
        icon: ['fab', 'node'],
        text: 'From Web to Game development and everything in between, I am capable of designing and implementing simple, yet efficient systems. Also, I am always willing to learn new technologies to continue growing my capabilities.'
      },{
        icon: ['fab', 'apple'],
        text: 'From Web to Game development and everything in between, I am capable of designing and implementing simple, yet efficient systems. Also, I am always willing to learn new technologies to continue growing my capabilities.'
      },{
        icon: 'object-group',
        text: 'From Web to Game development and everything in between, I am capable of designing and implementing simple, yet efficient systems. Also, I am always willing to learn new technologies to continue growing my capabilities.'
      },
    ]

    return <div
      id="WhoAmI"
      ref={el => this.container = el}
    >
      <div className="ContentContainer">
        <div className="row flex-center">
          { this.props.data.map((item, index) => {
              return (
                <div className="feature-container col-md-6 col-xl-4" ref={el => this.appendElement(el)} key={index}>
                  <Feature text={item.text} icon={item.icon} />
                </div>
              )
            })
          }
        </div>
      </div>
    </div>;
  }
};

WhoAmI.defaultProps = {
  windowWidth: 960,
  windowHeight: 570
};

export default WhoAmI;



