'use strict';
import React from 'react';
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ParticlesConfig from './particles-config';
import settings from '../../util/settings';
import ReactGA from 'react-ga';
import Plx from 'react-plx';
import Particles from 'react-particles-js';

class Landing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      analyticsSubmitted: {
        introAnimDone: false
      }
    }
    this.AnimationElements = []
    this.svg = {letters: []}
  }
  componentDidMount() {
    TweenMax.to(this.container, 0.5, {autoAlpha: 1});
    TweenMax.set(this.AnimationElements, {autoAlpha: 0});

    if (!this.props.animDone) {
      var tl = new TimelineMax({paused: true, delay: 0.5, onComplete: ()=>{
        this.props.setState()
        this.furtherSetup();
      }})

      tl.fromTo(this.svg.path, 1, {strokeDashoffset: 1600}, {strokeDashoffset: 0, ease: Expo.easeOut})
      tl.staggerFromTo(this.svg.letters, 2, {autoAlpha: 0, x: 50}, {autoAlpha: 1, x: 0, ease: Expo.easeOut}, 0.1)

      tl.to(this.svg.path, 1, {strokeDashoffset: 1100, ease: Expo.easeOut, delay: 1})
      tl.to(this.svg.path, 0.5, {y: 300, ease: Expo.easeOut})
      tl.to(this.clipPath, 0.4, {height: 600, ease: Expo.easeOut, delay: -0.5})
      tl.to(this.svg.letters, 0.5, {autoAlpha: 0, ease: Expo.easeOut, delay: -0.5})
      tl.to(document.body, 0.5, {backgroundColor: settings.colors.primary, ease: Expo.easeOut, delay: 0.25})
      tl.to(this.brandSVG, 0.5, {fill: "#e2d0a1", ease: Expo.easeOut, delay: -0.5})

      tl.play();
    } else {
      TweenMax.to(document.body, 0.5, {backgroundColor: settings.colors.primary, ease: Expo.easeOut})
      this.furtherSetup();
    }
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    TweenMax.set(this.container, {autoAlpha: 0});
  }
  componentDidUpdate() {
    if (this.GSVGFadeout) TweenMax.to(this.GSVGFadeout, 0.5, {autoAlpha: 0, delay: 1})
    if (this.Particles) TweenMax.to(this.Particles, 0.5, {opacity: 1, ease: Power1.easeInOut})
  }
  furtherSetup = () => {
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll()
    this.props.refSetter(this.container, this.props.refOrder)

    if (!this.state.analyticsSubmitted.introAnimDone) {
      ReactGA.modalview('landing');
      this.setAnalyticSubmission('introAnimDone')
    }
  }
  setAnalyticSubmission = (name) => {
    if (name) {
      this.setState({analyticsSubmitted: {...this.state.analyticsSubmitted, [name]: true}})
    }
  }
  animateIn = () => {
    var tl = new TimelineMax({paused: true, delay: 0.5});
    tl.staggerFromTo(this.AnimationElements, 2, {autoAlpha: 0, y: 50}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 0.25);
    tl.play();
  }
  handleScroll = () => {
    if (this.container.getBoundingClientRect().top < window.innerHeight) {
      this.animateIn();
      window.removeEventListener('scroll', this.handleScroll);
    }
  }
  render() {
    var xOffset = 150, yOffset = 150 - 20, spacing = 50,
        name = [..."B E T A".split(" ")];
        // spacing = 25,
        // name = [..."R A F I G E O R G E".split(" ")];
    xOffset = 250 - (name.length/2 * spacing)

    this.svg.letters = []

    this.AnimationElements = []

    return <div
      id="Landing"
      ref={el => this.container = el}
    >
      { !this.props.animDone ?
        (
          <div>
            <svg className="brand-svg" width="200" height="200" viewBox="0 0 100 100">
              <g transform="scale(0.15)">
                <defs>
                  <clipPath id="brand-clip-path">
                    <rect x="0" y="0" width="600" height="0" ref={el => this.clipPath = el} />
                  </clipPath>
                </defs>
                <path fillRule="evenodd" ref={el => this.brandSVG = el} clipPath="url(#brand-clip-path)" d="M535.000,129.000 C535.000,129.000 465.754,-11.230 271.000,1.000 C85.634,12.641 15.000,136.148 2.000,240.000 C-5.043,327.410 24.621,415.637 71.000,482.000 C148.574,580.109 285.094,596.414 338.000,591.000 C388.457,587.168 419.074,578.977 446.000,568.000 C472.543,557.348 497.707,542.285 509.000,532.000 C520.293,521.715 524.355,516.457 527.000,510.000 C529.476,503.956 530.329,499.396 531.000,490.000 C530.993,482.797 531.000,295.000 531.000,295.000 L500.000,295.000 C500.000,295.000 481.035,294.916 481.000,313.000 C480.965,331.084 481.000,493.000 481.000,493.000 C481.000,493.000 468.185,501.696 462.000,505.000 C455.815,508.304 442.572,514.460 436.000,517.000 C429.428,519.540 418.653,524.886 391.000,532.000 C369.869,537.053 337.754,548.564 255.000,525.000 C230.921,515.794 132.806,489.870 124.432,357.843 C120.228,276.578 124.587,226.690 176.000,165.000 C232.020,97.782 335.051,70.324 429.000,105.000 C448.241,112.631 470.631,123.673 495.000,144.000 C505.236,153.299 512.589,153.522 523.000,144.000 C534.900,132.705 535.000,129.000 535.000,129.000 ZM390.000,62.000 C390.000,62.000 215.746,26.074 128.000,157.000 C40.254,287.926 98.112,434.899 101.000,438.000 C103.888,441.101 13.466,311.271 69.000,181.000 C133.288,41.819 270.126,48.643 288.000,48.000 C305.874,47.357 371.059,51.064 390.000,62.000 Z" />
              </g>
            </svg>
            <div id="intro-svg">
              <svg width="500" height="300" viewBox="0 0 500 300">
                <path d="M1,1 L499,1 L499,299 L1,299 Z" ref={el => this.svg.path = el}/>
                { name && name.length &&
                  name.map((letter, index) => {
                    return (
                      <text x={xOffset + (index * spacing)} key={index}
                            y={yOffset + 35} ref={el => this.svg.letters[index] = el}>{letter}</text>
                    )
                  })
                }
              </svg>
            </div>
          </div>
        ) : (
          <div>

            <h5 className="anchored-text top">Rafi George</h5>
            <h5 className="anchored-text right">rafihgeorge@gmail.com</h5>

            { /* Particles */ }
            <div ref={el => this.Particles = el} style={{opacity: 0}}>
              <Particles  canvasClassName="particles-canvas"
                          params={ParticlesConfig} />
            </div>
            { /* G SVG */ }
            <svg className="brand-svg gradient" width="200" height="200" viewBox="0 0 100 100">
              <defs>
                <linearGradient y2="1" x2="1" y1="0" x1="0" id="brand-svg-gradient">
                 <stop stopColor={settings.shade(settings.colors.secondary, 0.2)} offset="0"/>
                 <stop stopColor={settings.shade(settings.colors.secondary, -0.3)} offset="1"/>
                </linearGradient>
              </defs>
              <g transform="scale(0.15)">
                <path fillRule="evenodd" d="M535.000,129.000 C535.000,129.000 465.754,-11.230 271.000,1.000 C85.634,12.641 15.000,136.148 2.000,240.000 C-5.043,327.410 24.621,415.637 71.000,482.000 C148.574,580.109 285.094,596.414 338.000,591.000 C388.457,587.168 419.074,578.977 446.000,568.000 C472.543,557.348 497.707,542.285 509.000,532.000 C520.293,521.715 524.355,516.457 527.000,510.000 C529.476,503.956 530.329,499.396 531.000,490.000 C530.993,482.797 531.000,295.000 531.000,295.000 L500.000,295.000 C500.000,295.000 481.035,294.916 481.000,313.000 C480.965,331.084 481.000,493.000 481.000,493.000 C481.000,493.000 468.185,501.696 462.000,505.000 C455.815,508.304 442.572,514.460 436.000,517.000 C429.428,519.540 418.653,524.886 391.000,532.000 C369.869,537.053 337.754,548.564 255.000,525.000 C230.921,515.794 132.806,489.870 124.432,357.843 C120.228,276.578 124.587,226.690 176.000,165.000 C232.020,97.782 335.051,70.324 429.000,105.000 C448.241,112.631 470.631,123.673 495.000,144.000 C505.236,153.299 512.589,153.522 523.000,144.000 C534.900,132.705 535.000,129.000 535.000,129.000 ZM390.000,62.000 C390.000,62.000 215.746,26.074 128.000,157.000 C40.254,287.926 98.112,434.899 101.000,438.000 C103.888,441.101 13.466,311.271 69.000,181.000 C133.288,41.819 270.126,48.643 288.000,48.000 C305.874,47.357 371.059,51.064 390.000,62.000 Z" />
              </g>
            </svg>

            { /* G SVG for fade-out effect */ }
            <svg className="brand-svg light" width="200" height="200" viewBox="0 0 100 100"
                 ref={el => this.GSVGFadeout = el}>
              <g transform="scale(0.15)">
                <path fillRule="evenodd" d="M535.000,129.000 C535.000,129.000 465.754,-11.230 271.000,1.000 C85.634,12.641 15.000,136.148 2.000,240.000 C-5.043,327.410 24.621,415.637 71.000,482.000 C148.574,580.109 285.094,596.414 338.000,591.000 C388.457,587.168 419.074,578.977 446.000,568.000 C472.543,557.348 497.707,542.285 509.000,532.000 C520.293,521.715 524.355,516.457 527.000,510.000 C529.476,503.956 530.329,499.396 531.000,490.000 C530.993,482.797 531.000,295.000 531.000,295.000 L500.000,295.000 C500.000,295.000 481.035,294.916 481.000,313.000 C480.965,331.084 481.000,493.000 481.000,493.000 C481.000,493.000 468.185,501.696 462.000,505.000 C455.815,508.304 442.572,514.460 436.000,517.000 C429.428,519.540 418.653,524.886 391.000,532.000 C369.869,537.053 337.754,548.564 255.000,525.000 C230.921,515.794 132.806,489.870 124.432,357.843 C120.228,276.578 124.587,226.690 176.000,165.000 C232.020,97.782 335.051,70.324 429.000,105.000 C448.241,112.631 470.631,123.673 495.000,144.000 C505.236,153.299 512.589,153.522 523.000,144.000 C534.900,132.705 535.000,129.000 535.000,129.000 ZM390.000,62.000 C390.000,62.000 215.746,26.074 128.000,157.000 C40.254,287.926 98.112,434.899 101.000,438.000 C103.888,441.101 13.466,311.271 69.000,181.000 C133.288,41.819 270.126,48.643 288.000,48.000 C305.874,47.357 371.059,51.064 390.000,62.000 Z" />
              </g>
            </svg>

            { /* Scroll Down SVG */ }
            <Plx className='scroll-icon'
                 parallaxData={[{
                   start: 0,
                   duration: 150,
                   properties: [
                     {
                       startValue: 1,
                       endValue: 0,
                       property: "opacity"
                     },
                     {
                       startValue: 0,
                       endValue: -50,
                       property: "translateY"
                     }
                   ]
                 }]}>
              <svg viewBox="0 0 247 390" onClick={this.props.scrollDown} ref={el => this.AnimationElements[0] = el}>
                <path id="wheel" d="M123.359,180 L123.359,250"/>
                <path id="mouse" d="M236.717,123.359c0,-62.565 -50.794,-113.359 -113.358,-113.359c-62.565,0 -113.359,50.794 -113.359,113.359l0,143.237c0,62.565 50.794,113.359 113.359,113.359c62.564,0 113.358,-50.794 113.358,-113.359l0,-143.237Z"/>
              </svg>
            </Plx>
          </div>
        )
      }
    </div>;
  }
};

Landing.defaultProps = {
  windowWidth: 960,
  windowHeight: 570
};

export default Landing;



