'use strict';
import React from 'react';
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import Recaptcha from 'react-google-recaptcha';
import settings from '../../util/settings';
import InputField from '../../components/InputField/InputField';
import fetch from 'isomorphic-fetch';
import ReactGA from 'react-ga';
import ParticleEffectButton from 'react-particle-effect-button'

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      validCaptcha: false,
      showError: false,
      submitted: false,
      emailSent: false,
      analyticsSubmitted: {
        scroll: false,
        form: false
      },
      btn: {
        hidden: false,
        animating: false
      }
    }
    this.AnimationElements = []
    this.InputFields = []
  }
  componentDidMount() {
    TweenMax.to(this.container, 0.5, {autoAlpha: 1});
    TweenMax.set(this.AnimationElements, {autoAlpha: 0});
    this.animateIn();

    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll()
    this.props.refSetter(this.container, this.props.refOrder)
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    TweenMax.set(this.container, {autoAlpha: 0});
  }
  handleInputChange = (e, item)=>{

  }
  handleScroll = (e) => {
    if (this.container.getBoundingClientRect().top < window.innerHeight && !this.state.analyticsSubmitted.scroll) {
      ReactGA.modalview('contact');
      this.setAnalyticSubmission('scroll')
    }
    if (this.props.isResponsive) {
      if (this.container.getBoundingClientRect().top <= window.innerHeight/2) {
        this.animateIn();
        window.removeEventListener('scroll', this.handleScroll);
      }
    } else {
      if (this.container.getBoundingClientRect().top < window.innerHeights) {
        this.animateIn();
        window.removeEventListener('scroll', this.handleScroll);
      }
    }
  }
  setAnalyticSubmission = (name) => {
    if (name) {
      this.setState({analyticsSubmitted: {...this.state.analyticsSubmitted, [name]: true}})
    }
  }
  animateIn = () => {
    var tl = new TimelineMax({paused: true});
    tl.staggerFromTo(this.AnimationElements, 2, {autoAlpha: 0, y: 50}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 0.25);
    tl.play();
  }
  handleReCaptcha = (value) => {
    this.setState({
      validCaptcha: value
    })
  }
  handleSubmit = (e) => {
    e.preventDefault();

    var allValid = true;
    for (var i = 0; i < this.InputFields.length; i++) {
      if (this.InputFields[i].id !== "website") {
        if (!this.InputFields[i].state.valid) {
          allValid = false;
          break;
        }
      }
    }
    if (!allValid) {
      this.setState({showError: true})
    } else if (!this.state.validCaptcha) {

    }
    else {
      fetch(settings.server + 'mail.php', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          "Content-type": "application/x-www-form-urlencoded"
        },
        body: JSON.stringify(this.getFormContent())
      }).then((response) => {
        if (!response.ok) {
          throw new Error('Something went wrong');
        } else {
          this.setState({emailSent: true})
        }
        ReactGA.event({
          category: 'Contact',
          action: 'Submit',
          label: response.ok ? 'Succeeded' : 'Failed'
        });
        return response.json();
      }).catch(err => {})
      this.setState({submitted: true});
      this.toggleButton()
    }
  }
  getFormContent = () => {
    var obj = {};
    for (var i = 0; i < this.InputFields.length; i++) {
      if (this.InputFields[i].id === "website") obj["website"] = this.InputFields[i].value;
      else {
        obj[this.InputFields[i].props.id] = this.InputFields[i].state.value
      }
    }
    return obj;
  }

  toggleButton = () => {
    if (this.state.btn.animating) return
    this.setState({btn: {
      hidden: !this.state.btn.hidden,
      animating: true
    }})
  }

  render() {
    this.AnimationElements = []
    this.InputFields = []

    return <div
      id="Letsconnect"
      ref={el => this.container = el}
    >
      <div className="ContentContainer">
        <h1 ref={el => this.AnimationElements[0] = el}>Let&#8217;s Connect</h1>
        {
          !this.state.submitted ? (
            <form onSubmit={this.handleSubmit}>
              <div className="row" ref={el => this.AnimationElements[1] = el}>
                <div className="col-sm-6">
                  <InputField label="Name" id="name" showError={this.state.showError}
                              ref={el => this.InputFields[0] = el}
                              onChange={this.handleInputChange} isRequired={true}/>
                </div>
                <div className="col-sm-6">
                  <InputField label="Email" id="email" isRequired type="email" showError={this.state.showError}
                              ref={el => this.InputFields[1] = el} />
                </div>
              </div>
              <input id="website" style={{display: "none"}}
                     value="www.rafigeorge.com" readOnly
                     ref={el => this.InputFields[2] = el} />
              <div ref={el => this.AnimationElements[2] = el}>
                { !settings.isDev &&
                  <div>
                    <Recaptcha
                      key="captcha"
                      className="captcha"
                      onChange={this.handleReCaptcha}
                      sitekey={settings.googleReCaptchaKey}
                      theme="dark"
                    />
                    <h6 style={{color: "red", marginTop: 10}} className="thin">{this.state.showError && !this.state.validCaptcha ? "Please complete the reCAPTCHA" : ""}</h6>
                  </div>
                }
              </div>
              <div ref={el => this.AnimationElements[3] = el}>
                <ParticleEffectButton
                    color={settings.colors.secondary}
                    hidden={this.state.btn.hidden} >
                  <button className="particle-submit">SEND</button>
                </ParticleEffectButton>
              </div>
              {
                // <div className="submit" ref={el => this.AnimationElements[3] = el}>
                //   <div className="submit-background"></div>
                //   <input type="submit" value="SUBMIT" disabled={this.state.submitted} />
                // </div>
              }
            </form>
          ) : (
            <div className="row">
              <div className="col-sm-6">
                <h2>Thanks for your email, I will get back to you shortly.</h2>
              </div>
            </div>
          )

        }
      </div>
    </div>;
  }
};

Contact.defaultProps = {
  windowWidth: 960,
  windowHeight: 570
};

export default Contact;



