import React, { Component } from "react";
import Page from "../layouts/main";
import Landing from "../sections/Landing/Landing";
import Work from "../sections/Work/Work";
import Contact from "../sections/Contact/Contact";
import WhoAmI from "../sections/WhoAmI/WhoAmI";
import settings from "../util/settings";
import Head from "next/head";
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import {enableScroll, disableScroll, addScrollEventListener, removeScrollEventListener} from '../util/scroll-control';
import ReactGA from 'react-ga';
import Plx from 'react-plx';
import data from '../static/json/data.json';

class Index extends Component {

  constructor(props) {
    super(props);
    this.state = {
      animDone: true,
      detect: {}
    }
    this.scrollInitiated = false
    this.sections = []
  }

  componentDidMount() {
    this.setState({detect: this.Page.getDetect()})
    // addScrollEventListener(this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('mousemove', this.handleMouseMove)
    removeScrollEventListener(this.handleScroll);
  }

  setAnimDone = () => {
    this.setState({animDone: true})
  }

  handleMobileScroll = (e) => {
    var name = "";
    for (var i = this.sections.length - 1; i > 0 ; i--) {
      if (this.sections[i].getBoundingClientRect().top < window.innerHeight/2) {
        name = this.getSectionName(i)
        break;
      }
    }
    if (name) {
      this.Page.setBackgroundColor(name)
    }
  }

  handleScroll = () => {
    if (!this.scrollInitiated) {
      this.scrollInitiated = true

      disableScroll();
      removeScrollEventListener(this.handleScroll)

      var directionDown = false, directionUp = false, e = event;
      if (e.type.indexOf("wheel") > -1) {
        if (e.deltaY > 0) directionDown = true;
        else if (e.deltaY < 0) directionUp = true;
      }

      var nextSec = "", nextSecIndex = -1;
      for (var index = 0; index < this.sections.length; index++) {
        let section = this.sections[index];
        if (directionDown) {
          let secTop = section.getBoundingClientRect().top;
          if (secTop >= window.innerHeight) {
            if (index <= this.sections.length - 1) {
              nextSecIndex = index;
              nextSec = index;
            }
            break;
          }
        } else if (directionUp) {
          let secBottom = section.getBoundingClientRect().bottom;
          if (secBottom > 50) {
            if (index > 0) {
              nextSecIndex = index - 1;
              nextSec = this.sections[nextSecIndex];
            }
            break;
          }
        }
      }
      if (nextSecIndex > -1) {
        let secName = this.getSectionName(nextSecIndex);
        this.Page.scrollToSection(secName)
        setTimeout(()=>{
          addScrollEventListener(this.handleScroll)
          enableScroll();
          this.scrollInitiated = false
        }, 1000)
      } else {
        addScrollEventListener(this.handleScroll)
        enableScroll();
        this.scrollInitiated = false
      }
    }
  }

  handleMouseMove = ()=>{
    if (this.Page.setCursorPaused) {
      var offset = 25, rect = {}, overlapping = false;
      for (var el in this.hitObjs) {
        if (this.hitObjs[el].obj) {
          rect = this.hitObjs[el].obj.getBoundingClientRect();
          overlapping = (event.clientX > rect.left - offset) && (event.clientX < rect.right + offset)
                            && (event.clientY > rect.top - offset) && (event.clientY < rect.bottom + offset);
          if (overlapping) break;
        }
      }
      var dimensions = {height: rect.height, width: rect.width, left: rect.left + window.pageXOffset, top: rect.top + window.pageYOffset, borderRadius: this.hitObjs[el].borderRadius}
      this.Page.setCursorPaused(overlapping, dimensions)
    }
  }

  getSectionName = (index) => {
    return (index > -1 && index <= this.sections.length) ? this.sections[index].id.substr(this.sections[index].id.indexOf("-")+1) : "";
  }

  getSectionIndex = (name) => {
    if (name) {
      for (var i = 0; i < this.sections.length; i++) {
        if (this.sections[i].id.indexOf(name) > -1) return i;
      }
    }
    return -1;
  }

  appendSection = (section, index) => {
    this.sections[index] = section
  }

  getDocHeight = ()=> {
    return Math.max( document.body.scrollHeight, document.body.offsetHeight )
  }

  scrollDown = ()=> {
    if (this.Page) this.Page.scrollToSection('Work')
  }

  render() {
    this.sections = []

    return (
      <Page id="Home" showMenu={this.state.animDone} ref={el => this.Page = el}>

        <Landing refSetter={this.appendSection}
                 refOrder={0}
                 scrollDown={this.scrollDown}
                 animDone={this.state.animDone}
                 setState={this.setAnimDone} />

        { this.state.animDone && (
            <div>
              <Work data={data.projects} refSetter={this.appendSection} refOrder={1} />
              <WhoAmI data={data.features} refSetter={this.appendSection} refOrder={2} />
              <Contact refSetter={this.appendSection} refOrder={3} />
            </div>
          )
        }

      </Page>
    )
  }
}

export default Index



