import React, { PureComponent } from 'react';
import PropTypes from 'prop-types'; // ES6
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import fa from '@fortawesome/fontawesome';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import { faCheckSquare, faCoffee } from '@fortawesome/fontawesome-free-solid'
import brands from '@fortawesome/fontawesome-free-brands'

export default class Feature extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
    fa.library.add(brands, faCheckSquare, faCoffee);
  }

  render() {
    return (
      <div className="Feature" ref={el => this.Feature = el}>
        <div className="feature-icon">
          <FontAwesomeIcon icon={this.props.icon} />
        </div>
        <p className="feature-content">{this.props.text}</p>
      </div>
    );
  }
}

Feature.propTypes = {
  text: PropTypes.string
};

Feature.defaultProps = {
  text: ""
};



