import React, { PureComponent } from 'react';
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import {enableScroll, disableScroll} from '../../util/scroll-control';

export default class Menu extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: true
    }
    this.Links = [];
  }

  componentDidMount() {
    this.fadeIn();
  }

  getTheme = () => {
    return this.props.theme ? this.props.theme : "dark";
  }

  fadeIn = ()=>{
    var tl = new TimelineMax({paused: true});
    tl.fromTo(this.MenuButton, 0.5, {x: -70}, {x: 0, ease: Expo.easeOut});
    tl.staggerTo([...this.Links], 1, {opacity: 1, ease: Expo.easeOut}, 0.125);
    tl.play();
  }

  toggle = () => {
    var callback = ()=>{
      this.setState({collapsed: !this.state.collapsed})
    }
    let time = 0.25;
    let children = this.MenuButtonSVG.children;

    if (this.state.collapsed) {
      //Expand
      var tl = new TimelineMax({paused: true});
      tl.to(this.Menu, time*2, {x: this.Menu.getBoundingClientRect().width, ease: Quad.easeOut});
      tl.to(children[0], time, {y: 9, delay: -time*2});
      tl.to(children[2], time, {y: -9, delay: -time});
      tl.to([children[0],children[1],children[2]], time/2, {strokeDashoffset: 30});
      tl.fromTo([children[3],children[4],children[5],children[6]], time/2, {strokeDashoffset: -22}, {strokeDashoffset: 5, onComplete: callback});
      tl.play();
    } else {
      //Collapse
      var tl = new TimelineMax({paused: true, delay: 0.25});
      tl.to(this.Menu, time*2, {x: 0, ease: Back.easeOut});
      tl.fromTo([children[3],children[4],children[5],children[6]], time/2, {strokeDashoffset: 5}, {strokeDashoffset: -22, delay: -time*2});
      tl.to([children[0],children[1],children[2]], time/2, {strokeDashoffset: 0});
      tl.to(children[0], time, {y: 0});
      tl.to(children[2], time, {y: 0, delay: -time, onComplete: callback});
      tl.play();
    }

    this.props.moveContainer(this.state.collapsed)
  }

  goToSection = (name) => {
    if (name) {
      this.toggle();
      this.props.scrollToSection(name, enableScroll)
    }
  }

  render() {
    const theme = this.getTheme();
    this.Links = []
    return (
      <div className={"MenuContainer " + theme} ref={el => this.Menu = el}>
        <div className={"MenuButton " + theme}
             ref={el => this.MenuButton = el}
             onClick={this.toggle}>
             <svg className={"MenuButton " + theme} viewBox="0 0 30 30" ref={el => this.MenuButtonSVG = el}>
               { /* Collapsed */}
               <line className="collapsed" x1="0" y1="5" x2="30" y2="5" />
               <line className="collapsed" x1="0" y1="14" x2="30" y2="14" />
               <line className="collapsed" x1="0" y1="23" x2="30" y2="23" />
               { /* Expanded */}
               <line x1="0" y1="0"
                     x2="15" y2="15"
                     className="expanded" />
               <line x1="30" y1="30"
                     x2="15" y2="15"
                     className="expanded" />
               <line x1="0" y1="30"
                     x2="15" y2="15"
                     className="expanded" />
               <line x1="30" y1="0"
                     x2="15" y2="15"
                     className="expanded" />
             </svg>
        </div>
        <ul className={"MenuLinkList " + theme}
            ref={el => this.LinkList = el} >
          <li onClick={this.goToSection.bind(this, "Landing")}
              ref={el => this.Links[0] = el}
              className={theme}>
            <a>
              <span className="LinkNumber">01</span>
                <svg className="MenuLinkLine" viewBox="0 0 30 3">
                  <line x1="0" y1="2" x2="30" y2="2" />
                </svg>
              Home
            </a>
          </li>
          <li onClick={this.goToSection.bind(this, 'Work')}
              ref={el => this.Links[1] = el} >
            <a>
              <span className="LinkNumber">02</span>
                <svg className="MenuLinkLine" viewBox="0 0 30 3">
                  <line x1="0" y1="2" x2="30" y2="2" />
                </svg>
              Work
            </a>
          </li>
          <li onClick={this.goToSection.bind(this, 'WhoAmI')}
              ref={el => this.Links[2] = el} >
            <a>
              <span className="LinkNumber">03</span>
                <svg className="MenuLinkLine" viewBox="0 0 30 3">
                  <line x1="0" y1="2" x2="30" y2="2" />
                </svg>
              Who Am I
            </a>
          </li>
          <li onClick={this.goToSection.bind(this, 'Letsconnect')}
              ref={el => this.Links[3] = el} >
            <a>
              <span className="LinkNumber">04</span>
                <svg className="MenuLinkLine" viewBox="0 0 30 3">
                  <line x1="0" y1="2" x2="30" y2="2" />
                </svg>
              Let&#39;s Connect
            </a>
          </li>
        </ul>
      </div>
    );
  }
}

Menu.propTypes = {
};

Menu.defaultProps = {
};



